<template >
  <div class="lrcContainer">
    <div class="lrc" ref="lrc">
      {{ getAllKey }}
      <p
        class="lrc-p"
        :class="{active:parseInt(currentTime) >= keyArr[index] && parseInt(currentTime) < keyArr[index+1]}"
        v-for="(item,key,index) in lrcData"
        :key="index">
        {{ item }}{{ srcollLrc(key,index) }}
      </p>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      lrcData:{},   // {0:"",1:""......}   
      keyArr:[]     // ["0","1","3"...]  
    }
  },
  props:{
    songid:{
      type:[String,Number],
      default:""
    },
    currentTime:{
      type:[String,Number],
      default:0
    },
    // durationTime:{
    //   type:[String,Number],
    //   default:0
    // }
  },
  created(){  
    //const LRCUrl = `/baiduapi/restserver/ting?method=baidu.ting.song.lry&songid=${this.songid}`;
   const LRCUrl = `http://tingapi.ting.baidu.com/v1/restserver/ting?method=baidu.ting.song.lry&songid=${this.songid}`;
    this.axios.get(LRCUrl)
     .then(res => {
       //  
       var lyrics = res.data.lrcContent.split("\n");   // 
      //  console.log(lyrics)
        var lrcObj = {};
        for(var i = 0 ;i<lyrics.length;i++){
          var lyric = decodeURIComponent(lyrics[i]); //
          //encodeURIComponent;[ti:] / [00:00.89]
          var timeReg = /\[\d*:\d*((\.|\:)\d*)*\]/g;  //  [ :: (.:) ]
          var timeRegExpArr = lyric.match(timeReg);   //, ["[00:00.89]"]
          if(!timeRegExpArr)continue;                 //   
          var clause = lyric.replace(timeReg,'');     //''    caluse = 
          // console.log(timeRegExpArr)
          // console.log(clause) 
          var t = timeRegExpArr[0];    //  t  = [00:00.89]
          var min = Number(String(t.match(/\[\d*/i)).slice(1)),  
          //  ["[00"]     [  "00"   0  
          sec = Number(String(t.match(/\:\d*/i)).slice(1));
          //   [":00.89]"]   :    00.89
          var time = min * 60 + sec;  //  audio   currentTimedurationTime 
          lrcObj[time] = clause;   // number obj[]   lrcObj = {90: ""}
        } 
          this.lrcData = lrcObj;
          //  console.log(this.lrcData)  {0:"",1:""......}
     })
     .catch(error => {
       console.log(error);
     })
  },
  computed:{
    getAllKey(){
      for(var key in this.lrcData){  //()
        this.keyArr.push(key);  // this.keyArr = ["0","1","3"]   
      }
    }
  },
  methods:{
    srcollLrc(key,index){
      const lrcDiv = this.$refs.lrc
       if(key < this.currentTime){
           lrcDiv.style.top = -((index-2)*30)+"px"
       }
    }
  },


}

</script>

<style scoped>

.lrcContainer{
  width: 100%;
  height: 150px;
  overflow: hidden;
  position: relative;
}

.lrc{
	width: 100%;
  position: absolute;
  top: 60px;
}
.active{
  color: red !important;
}
.lrc-p{
  height: 30px;
	line-height: 30px;
}

</style>
